import React, { Component } from 'react';
import style from './TheaterInfo.module.css'
class TheaterInfo extends Component {
    render() {
        return (
            <div className={style.contents}>
                <h3>
                    <img src="https://img.cgv.co.kr/R2014/images/title/h3_theater.gif"/>
                </h3>
                <h2>CGV강남</h2>
                <div className={style.theater_info}>
                    <img src="https://img.cgv.co.kr/Theater/Theater/2014/1211/CGVgangnam.jpg"></img>
                    <div>
                        <p>서울특별시 강남구 역삼동 814-6 스타플렉스</p>
                        <p style={{paddingTop:"0px"}}>서울특별시 강남구 강남대로 438 (역삼동)</p>
                        <p>1544-1122</p>
                        <p style={{paddingTop:"0px"}}>6관 / 874석</p>
                    </div>
                </div>

            </div>
        );
    }
}

export default TheaterInfo;